
import $ from 'jquery';
var wow = require('./wow.js');
var timer = require('./timer.js');


var carousel = require('./jquery.jcarousel.min.js');
var modalPopup = require('./jquery.arcticmodal-0.3.min.js');
var fancy = require('./jquery.fancybox.pack.js');
var mask = require('./jquery.mask.min.js');
var ajax = require('./ajax.js')

// продукция
   	var item = $('.nav-product__list li.active').attr('id');

   	$('.catalog__wrap.'+item).addClass('active').css('opacity', '1').siblings('.catalog__wrap').css('opacity', '0');
   	$('.nav-product__list li').click(function(){
        var item = $(this).attr('id');
        $(this).addClass('active').siblings().removeClass('active');
        var item = $('.nav-product__list li.active').attr('id');
        $('.catalog__wrap.'+item).addClass('active').siblings('.catalog__wrap').removeClass('active');
        $('.catalog__wrap').animate({
			opacity: 0

		}, 300, function() {
			$('.catalog__wrap.'+item).animate({
				opacity: 1
			}, 300);
		});
   	});


 // комплекты
   	var item = $('.nav-complect__list li.active').attr('id');

   	$('.complect__wrap.'+item).addClass('active').css('opacity', '1').siblings('.complect__wrap').css({'opacity':'0', display:'none'});
   	$('.nav-complect__list li').click(function(){
        var item = $(this).attr('id');
        $(this).addClass('active').siblings().removeClass('active');
        var item = $('.nav-complect__list li.active').attr('id');
        $('.complect__wrap.'+item).addClass('active').siblings('.complect__wrap').removeClass('active');
        $('.complect__wrap').css("display","none").animate({
			opacity: 0

		}, 300, function() {
			$('.complect__wrap.'+item).css("display","block").animate({
				opacity: 1
			}, 300);
		});
   	});


// FANCYBOX

$(".fancybox").fancybox(

	{padding:1}

);


// CAROUSEL

$(function() {
  // Инициализация слайдера
  $('.jcarousel').jcarousel({
    wrap: 'circular'
  });

  $('.jcarousel-prev').jcarouselControl({
      target: '-=1'
  });

  $('.jcarousel-next').jcarouselControl({
      target: '+=1'
  });
});


//popup-video

$('.video .video__play').click(function() {
	$('#videoModal').arcticmodal();
})


// popup form

$('.btn-popup').click(function(e) {
	e.preventDefault();
	$('#formModal').arcticmodal();
})



//SCROLL

$('.main-nav a[href^="#"]').click(function(){
        var target = $(this).attr('href');
        var top = $(target).offset().top;
        $('html, body').animate({scrollTop: top}, 500);
        return false; 
});

// mask
$('.tel').mask('+7(000)000-0000');





