//form

$('.form-block').click(function(e) {
	
	if($(e.target).hasClass('form-btn')){
		e.preventDefault();
		validete_form("name_form","phone_form","title_form", $(this));
	}	

	
})



function validete_form(name,phone,title,that){
		//регулярные выражения
		var reg_name = /[a-zа-я0-9]+$/i;
		var reg_phone = /[0-9]+$/i;

		var form = that.find('form');

		var name_val = that.find('input[name="' + name + '"]').val();
		var phone_val = that.find('input[name="' + phone + '"]').val();
		var title_val = that.find('input[name="' + title + '"]').val();
		if(reg_name.test(name_val) && name_val.length > 2){
			
		}else{
			red_border(name,that);
		}

		if(reg_phone.test(phone_val) && phone_val.length > 7){
			
		}else{
			red_border(phone,that);
		}

		if(reg_name.test(name_val) && name_val.length > 2 && reg_phone.test(phone_val) && phone_val.length > 7 ){
			ajax_zapros(form);//если все окей делаем запрос на отправку данных
			
		}
}

function red_border(input_name,that){ 
		var name = 'input[name="' + input_name + '"]';
		that.find(name).css({'border-bottom':'1px solid red'});
		setTimeout(function(){
			that.find(name).css({'border-bottom':'1px solid #dadada'});
		},3000);
	}


function ajax_zapros(form){ 
		
		form.submit(function() {
			
			$.ajax({
				type:"POST",
				url:"mail.php",
				data:$(this).serialize()
			}).done(function() {
				$('#closeForm').arcticmodal();
				//$('input[name="' + name + '"]').attr("value","");
				//$('input[name="' + phone + '"]').attr("value","");
			});
			return false
		})
		
		
	}


